export const ofoMapType = {
  NEARBY_CAR: 1, // 附近车辆
  RIDING_ROUTE: 2, // 骑行路线
  CUR_LOCATION: 3 // 当前位置
}
export default {
  props: {
    type: ''
  },
  data () {
    return {
      // 定位出错信息
      locErrorMsg: null,
      map: null, // google地图实例
      marker: null, // 用户位置的标注
      nearbyofoCars: [], // 周边ofo位置数据
      // infowindow: null, // 位置信息展示视图
      isMapCreated: false
    }
  },
  methods: {
    // 定位
    findMe () {
      return new Promise(async (resolve, reject) => {
        try {
          const coordinate = await this.$store.dispatch('asyncGetCoordinate')
          // 设置地图位置中心
          if (!this.map) this.map = this.createMap(coordinate)
          else this.map.setCenter(coordinate)
          if (this.type !== ofoMapType.RIDING_ROUTE) {
            // 渲染位置标注
            if (!this.marker) this.marker = this.createMarker()
            this.marker.setPosition(coordinate)
          }
          // 渲染位置信息
          // if (!this.infowindow) this.infowindow = this.createInfowindow()
          try {
            // 经纬度解码为位置信息
            // const {formatted_address: formattedAddress} = await this.geocode(coordinate)
            // this.infowindow.setContent(formattedAddress)
            // this.infowindow.open(this.map, this.marker)
          } catch (e) {
            console.error(e)
          }
          resolve(coordinate)
        } catch (err) {
          switch (err.code) {
            case err.TIMEOUT:
              this.locErrorMsg = 'Connection timeout. Please refresh the page and try again'
              break
            case err.PERMISSION_DENIED:
              this.locErrorMsg = 'You have denied the location sharing service, the query has been cancelled'
              break
            case err.POSITION_UNAVAILABLE:
              this.locErrorMsg = 'Sorry, location service is currently unavailable'
              break
            default:
              this.locErrorMsg = "Sorry, please accept your browser's permission request"
          }
          console.error(err)
          reject(err)
        }
      })
    },
    // 获取周边车辆数据
    async getNearCars () {
      if (this.type !== ofoMapType.NEARBY_CAR) {
        return
      }
      try {
        const {data: {cars}} = await $API.ofo.nearbyofoCar()
        cars.forEach(({lat, lng}) => {
          return new window.google.maps.Marker({
            position: {lat, lng},
            map: this.map,
            scale: 10,
            icon: {
              url: '../../../../../static/img/ofo/nearcar@2x.png',
              scaledSize: new window.google.maps.Size(51, 57)
            },
            zIndex: 20,
            title: 'ofo'
          })
        })
      } catch (e) {
        console.error(e)
      }
    },
    // 展示当前位置信息
    /* createInfowindow () {
      const infowindow = new window.google.maps.InfoWindow({
        disableAutoPan: true // 阻止地图滑动
      })
      return infowindow
    }, */
    // 经纬度解码为位置信息
    geocode (location) {
      const geocoder = new window.google.maps.Geocoder()
      return new Promise((resolve, reject) => {
        geocoder.geocode({location}, (results, status) => {
          if (status === 'OK' && results.length > 0) {
            const address = results[0]
            resolve(address)
          } else {
            reject(new Error('经纬度位置解码失败'))
          }
        })
      })
    },
    // 重新定位
    async reLocate () {
      try {
        await this.findMe()
        // 获取周边车辆数据
        this.getNearCars()
      } catch (e) {
        console.error(e)
      }
    },
    // 创建google地图实例
    createMap (center) {
      const map = new window.google.maps.Map(document.getElementById('nearby_ofo_map'), {
        mapTypeControl: false,
        disableDefaultUI: true,
        scaleControl: true,
        panControl: true,
        streetViewControl: true,
        fullscreenControl: false,
        zoom: 16,
        center
      })
      this.isMapCreated = true
      this.$emit('mapCreated')
      return map
    },
    // 创建用户位置的标注实例
    createMarker () {
      var image = {
        url: '../../../../../static/img/ofo/location-mark@2x.png',
        origin: new window.google.maps.Point(0, 0),
        anchor: new window.google.maps.Point(50, 50),
        scaledSize: new window.google.maps.Size(100, 100)
      }
      return new window.google.maps.Marker({
        icon: image,
        zIndex: 50,
        map: this.map
      })
    }
  },
  async mounted () {
    // 获取当前位置
    await this.findMe()
    // 获取周边车辆数据
    this.getNearCars()
  }
}



